@use "../style/tokens";
@use "../style/color" as colors;
@use "sass:color";

$base: ".gnt-card-base";

#{$base} {
  background-repeat: no-repeat;
  background-size: cover;
  cursor: default;
  display: grid;
  grid-template-areas: "main";

  &:focus {
    outline: none;
  }
}

#{$base}--is-clickable {
  border-radius: tokens.$size-choice-border-radius-6;
  overflow: hidden;
  user-select: none;

  &:hover {
    box-shadow: colors.f-get-boxshadow("outline" "white25"), colors.f-get-boxshadow("default");
  }
}

#{$base}--has-aspectratio::after {
  $ratio: (9 / 16) * 100%;
  content: "";
  grid-area: main;
  padding-bottom: #{$ratio};
  pointer-events: none;
}

#{$base}__content {
  align-content: end;
  display: grid;
  grid-area: main;
  grid-gap: tokens.$size-choice-padding-8;
  grid-template-areas: "main";
  padding: var(--content-padding);
}

#{$base}__content--gradient-horizontal {
  $base-color: rgba(tokens.$color-choice-pureblack, 0.85);
  $gradient: colors.f-gradient-smooth($base-color, color.adjust($base-color, $alpha: -1));
  background-image: linear-gradient(to right, $gradient);
  background-repeat: no-repeat;
  justify-self: flex-start;
  padding-right: calc(var(--content-padding) * 4);
}

#{$base}__content--gradient-vertical {
  $base-color: rgba(tokens.$color-choice-pureblack, 0.85);
  $gradient: colors.f-gradient-smooth($base-color, color.adjust($base-color, $alpha: -1));
  align-self: flex-end;
  background-image: linear-gradient(to top, $gradient);
  background-repeat: no-repeat;
  padding-top: calc(var(--content-padding) * 2);
}
