@use "../style/utils";
@use "../style/tokens";
@use "../style/color";

$base: ".gnt-dropdown";

#{$base} {
  @include utils.m-transition(box-shadow, out);

  background-color: tokens.$color-choice-grayscale-40;
  border-radius: tokens.$size-choice-border-radius-4;
  display: grid;
  grid-template-columns: 1fr auto;
  min-width: tokens.$size-choice-height-48 * 2;
  padding: 0 0 0 tokens.$size-choice-padding-12;
  place-items: center start;
  user-select: none;

  &--variant-xsmall {
    height: tokens.$size-choice-height-24 - tokens.$size-choice-padding-4;
  }

  &--variant-small {
    height: tokens.$size-choice-height-24;
  }

  &--variant-large {
    height: tokens.$size-choice-height-32;
  }

  &:focus {
    outline: none;
  }

  &:hover {
    @include utils.m-transition(box-shadow, in);
    box-shadow: color.f-get-boxshadow(("outline" "gray70"));
  }
}

#{$base}__value {
  cursor: default;
  padding: 0 tokens.$size-choice-padding-32 0 0;
}

#{$base}__icon {
  align-items: center;
  align-self: stretch;
  border-left: 2px solid tokens.$color-choice-grayscale-50;
  display: grid;
  min-height: 1px;
  place-content: center;
  width: tokens.$size-choice-height-32;

  &:hover > * {
    @include utils.m-transition(opacity, in);
    opacity: 1;
  }

  > * {
    @include utils.m-transition(opacity, out);

    flex: 1 1 auto;
    opacity: 0.25;
  }
}

#{$base}__popup {
  border-radius: tokens.$size-choice-border-radius-6;
  box-shadow: color.f-get-boxshadow("default");
  overflow: hidden;
  z-index: 1;
}

#{$base}__option {
  background-color: tokens.$color-choice-grayscale-70;
  display: grid;
  gap: tokens.$size-choice-padding-8;
  grid-template-areas: "icon label";
  grid-template-columns: tokens.$size-choice-height-24 1fr;
  height: tokens.$size-choice-height-32;
  padding: 0 tokens.$size-choice-padding-8;
  place-items: center start;

  &--hovered {
    background-color: tokens.$color-choice-grayscale-60;
    outline: none;
  }

  &--current {
    background: tokens.$color-choice-grayscale-50;
  }

  &--variant-xsmall {
    height: tokens.$size-choice-height-24;
  }

  &--variant-small {
    height: tokens.$size-choice-height-24;
  }

  &--variant-large {
    height: tokens.$size-choice-height-32;
  }
}

#{$base}__check {
  grid-area: check;
  pointer-events: none;
}

#{$base}__option-label {
  cursor: default;
  grid-area: label;
  pointer-events: none;
}
