@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$base: ".gnt-menu-item";

#{$base}-wrap {
  @include utils.m-btn-reset;

  background-color: tokens.$color-choice-grayscale-70;

  &:focus {
    outline: none;
  }
}

#{$base} {
  align-items: center;
  border-radius: tokens.$size-choice-border-radius-4;
  cursor: default;
  display: grid;
  grid-auto-flow: column;
  grid-gap: tokens.$size-choice-padding-8;
  grid-template-areas: "main chevron";
  grid-template-columns: 1fr auto;
  margin: 0 tokens.$size-choice-padding-8;
  max-width: 100%;
  overflow: hidden;
  padding: 0 tokens.$size-choice-padding-8;
  user-select: none;
  white-space: nowrap;
}

#{$base}:focus,
#{$base}-wrap--can-self-highlight:hover #{$base},
#{$base}--has-highlight {
  background-color: tokens.$color-choice-grayscale-60;
  outline: none;
}

#{$base}--variant-small {
  height: tokens.$size-choice-height-24;
}

#{$base}--variant-large {
  height: tokens.$size-choice-height-32;
}

#{$base}--is-checkable {
  grid-template-areas: "check main chevron";
  grid-template-columns: tokens.$size-choice-height-24 / 2 1fr auto;
}

#{$base}--has-icon {
  grid-template-areas: "icon main chevron";
  grid-template-columns: tokens.$size-choice-height-24 1fr auto;
}

#{$base}--has-icon#{$base}--is-checkable {
  grid-template-areas: "icon check main chevron";
  grid-template-columns: tokens.$size-choice-height-24 tokens.$size-choice-height-24 / 2 1fr auto;
}

#{$base}__icon {
  grid-area: icon;
}

#{$base}__check {
  display: grid;
  grid-area: check;
}

#{$base}__label {
  grid-area: main;
}

#{$base}__chevron {
  grid-area: chevron;
}
