@use "../style/tokens";
@use "../style/color";

$base: ".gnt-popup";

#{$base} {
  border-radius: tokens.$size-choice-border-radius-6;
  box-shadow: color.f-get-boxshadow("default");
  overflow: hidden;
}

#{$base}__header {
  align-items: center;
  background: tokens.$color-choice-grayscale-60;
  display: grid;
  grid-auto-flow: column;
  height: tokens.$size-choice-height-48;
  justify-content: space-between;
  padding: 0 tokens.$size-choice-padding-12 0 tokens.$size-choice-padding-16;
}

#{$base}__guts {
  background: tokens.$color-choice-grayscale-50;
  padding: tokens.$size-choice-padding-48;
}

#{$base}__footer {
  background: tokens.$color-choice-grayscale-50;
  border-top: tokens.$size-choice-border-width-2 solid tokens.$color-choice-grayscale-40;
  padding: tokens.$size-choice-padding-16;
}
