@use "../style/tokens";
@use "../style/color";
@use "../style/utils";
@use "../style/typography";

$base: ".gnt-input";

#{$base}-wrap {
  align-items: center;
  display: inline-grid;
  flex-flow: row nowrap;
  grid-template-areas:
    "icon-left main icon-right"
    "error error error";
  grid-template-columns: 0 auto 0;
  grid-template-rows: auto auto;
  line-height: 0;
}

#{$base} {
  background-color: tokens.$color-choice-grayscale-40;
  border-radius: tokens.$size-choice-border-radius-4;
  border-style: none;
  color: tokens.$color-choice-white-50;
  font-family: tokens.$font-choice-family-normal;
  grid-area: main;
  padding: 0 tokens.$size-choice-padding-12;

  &::placeholder {
    color: tokens.$color-choice-white-50;
  }

  &--size-small {
    @include typography.tvariant-body-small();
    height: tokens.$size-choice-height-24;
    padding: 0 tokens.$size-choice-padding-8;
  }

  &--size-large {
    @include typography.tvariant-body-medium();
    height: tokens.$size-choice-height-32;
  }

  &--has-icon-left {
    padding-left: tokens.$size-choice-padding-32;
  }

  &--has-icon-right {
    padding-right: tokens.$size-choice-padding-32;
  }

  &--has-value {
    color: tokens.$color-choice-white-100;
  }

  &--is-multiline {
    height: auto;
    padding: tokens.$size-choice-padding-12 / 2 tokens.$size-choice-padding-12;
    resize: none;
  }

  &--is-number::-webkit-inner-spin-button {
    visibility: hidden;
  }

  &:disabled {
    color: tokens.$color-choice-white-25;
    pointer-events: none;
  }

  &:disabled:placeholder {
    color: tokens.$color-choice-white-25;
  }

  &:hover {
    box-shadow: color.f-get-boxshadow("outline" "gray70");
  }

  &:focus {
    box-shadow: color.f-get-boxshadow("outline" "blue");
    outline: none;
  }

  &--has-warning,
  &--has-warning:hover,
  &--has-warning:focus {
    box-shadow: color.f-get-boxshadow("outline" "orange");
  }

  &--has-error,
  &--has-error:hover,
  &--has-error:focus {
    box-shadow: color.f-get-boxshadow("outline" "red");
  }
}

#{$base}__icon-wrap {
  display: inline-block;
  width: 0;

  &--left {
    grid-area: icon-left;
    transform: translate(tokens.$size-choice-padding-4, 0);
  }

  &--right {
    $xoffset: tokens.$size-choice-height-24 + tokens.$size-choice-padding-4;
    grid-area: icon-right;
    transform: translate($xoffset * -1, 0);
  }
}

#{$base}__helper {
  grid-area: error;
  padding-top: tokens.$size-choice-padding-8;
}

#{$base}::-webkit-credentials-auto-fill-button {
  @include utils.m-transition(background-color, out);
  background-color: tokens.$color-choice-white-50;

  &:hover {
    @include utils.m-transition(background-color, in);
    background-color: tokens.$color-choice-white-100;
  }
}
