@use "../style/tokens";
@use "../style/utils";

$base: ".gnt-checkbox";

#{$base}-outerwrap {
  font-size: 0;
}

#{$base}-wrap {
  display: inline-block;

  &:focus {
    outline: none;
  }
}

#{$base} {
  align-items: flex-start;
  column-gap: tokens.$size-choice-padding-8;
  cursor: default;
  display: inline-grid;
  grid-auto-flow: column;

  &:focus {
    outline: none;
  }

  &--is-disabled {
    @include utils.m-unhoverable;
    pointer-events: none;
  }
}

#{$base}__input {
  display: none;
}

#{$base}__icon {
  @include utils.m-transition(opacity, out);

  &--is-disabled {
    opacity: 0.25;
  }

  #{$base}-wrap[aria-checked=false] &--is-enabled:hover {
    --icon-color: #{tokens.$color-choice-white-100};
    @include utils.m-transition(opacity, in);
    opacity: 1;
  }
}

#{$base}__icon > * {
  @include utils.m-transition(fill stroke, out);

  #{$base}:hover & {
    @include utils.m-transition(fill stroke, in);
  }
}

#{$base}__label {
  user-select: none;
  
  &--medium {
    margin-top: (tokens.$size-choice-height-24 - tokens.$size-choice-line-20) / 2;
  }

  &--small {
    margin-top: (tokens.$size-choice-height-24 - tokens.$size-choice-line-18) / 2;
  }
}

#{$base}__description {
  margin-left: tokens.$size-choice-padding-8 + tokens.$size-choice-height-24;

  &--medium {
    margin-top: tokens.$size-choice-padding-4;
  }
}
