@use "../style/tokens";
@use "../style/color";
@use "../style/typography";

.gnt-textlabel {
  $block: &;
  @include color.m-palette-colors($colors: color.$palette-colors);
  font-family: tokens.$font-choice-family-normal;
  -webkit-font-smoothing: antialiased;
  font-weight: tokens.$font-choice-weight-700;
  overflow: hidden;
  text-overflow: ellipsis;

  &--variant-heading {
    font-family: tokens.$font-choice-family-condensed;
    text-transform: uppercase;

    &#{$block}--size-xsmall {
      @include typography.tvariant-heading-xsmall;
    }
    &#{$block}--size-small {
      @include typography.tvariant-heading-small;
    }
    &#{$block}--size-medium {
      @include typography.tvariant-heading-medium;
    }
    &#{$block}--size-large {
      @include typography.tvariant-heading-large;
    }
    &#{$block}--size-xlarge {
      @include typography.tvariant-heading-xlarge;
    }
  }

  &--variant-body {
    &#{$block}--size-xsmall {
      @include typography.tvariant-body-xsmall;
    }
    &#{$block}--size-small {
      @include typography.tvariant-body-small;
    }
    &#{$block}--size-medium {
      @include typography.tvariant-body-medium;
    }
    &#{$block}--size-large {
      @include typography.tvariant-body-large;
    }
    &#{$block}--size-xlarge {
      @include typography.tvariant-body-xlarge;
    }
  }

  &--variant-caption {
    text-transform: uppercase;

    &#{$block}--size-small {
      @include typography.tvariant-caption-small;
    }
    &#{$block}--size-medium {
      @include typography.tvariant-caption-medium;
    }
    &#{$block}--size-large {
      @include typography.tvariant-caption-large;
    }
    &#{$block}--size-xlarge {
      @include typography.tvariant-caption-xlarge;
    }
  }

  &--variant-button {
    text-transform: uppercase;
    white-space: nowrap;

    &#{$block}--size-small {
      @include typography.tvariant-button-small;
    }
    &#{$block}--size-medium {
      @include typography.tvariant-button-medium;
    }
    &#{$block}--size-large {
      @include typography.tvariant-button-large;
    }
    &#{$block}--size-xlarge {
      @include typography.tvariant-button-xlarge;
    }
  }
}
