@use "../style/tokens";
@use "../style/color";

$base: ".gnt-menu";

#{$base} {
  background-color: tokens.$color-choice-grayscale-70;
  border-radius: tokens.$size-choice-border-radius-4 * 2;
  box-shadow: color.f-get-boxshadow("default");
  display: grid;
  max-width: 240px;
  min-width: 160px;
  padding: tokens.$size-choice-padding-8 0;
}

#{$base}__submenu {
  display: grid;
}

#{$base}__header {
  display: flex;
  padding: 0 tokens.$size-choice-padding-16;
  place-items: center start;

  &--size-small {
    height: tokens.$size-choice-height-24;
  }

  &--size-large {
    height: tokens.$size-choice-height-32;
  }
}

#{$base}__divider {
  background-color: tokens.$color-choice-grayscale-60;
  height: tokens.$size-choice-border-width-2;
  margin: tokens.$size-choice-padding-8 0;
  user-select: none;
}
