@use "../style/tokens";
@use "../style/utils";
@use "../style/icons";

$base: ".gnt-text-button";

#{$base} {
  @include utils.m-btn-reset();
  width: initial;

  &:focus {
    outline: none;
  }

  &--has-icon {
    align-items: center;
    display: inline-grid;
    grid-template-columns: auto 1fr;
  }
  
  &--is-disabled {
    @include utils.m-unhoverable;
    pointer-events: none;
  }

  &--icon-right #{$base}__icon {
    order: 2;
  }

  &--size-large {
    column-gap: tokens.$size-choice-padding-12;
  }

  &--size-medium {
    column-gap: tokens.$size-choice-padding-8;
  }

  &--size-small {
    column-gap: tokens.$size-choice-padding-4;
  }
}

#{$base} {
  @include utils.m-hoverable-onhover() {
    #{$base}__icon {
      @include icons.m-set-iconcolor(tokens.$color-choice-white-100);
    }

    #{$base}__icon path {
      @include utils.m-transition(fill stroke, in);
    }

    #{$base}__caption {
      @include utils.m-transition(color, in);
      color: tokens.$color-choice-white-100;
    }
  }
}

#{$base}__icon path {
  @include utils.m-transition(fill stroke, out);
}

#{$base}__caption {
  @include utils.m-transition(color, out);
}
