@use "../style/reset";
@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$button-variants: (
  "primary": tokens.$color-choice-blue,
  "secondary": tokens.$color-choice-white-10,
  "red": tokens.$color-choice-red,
  "green": tokens.$color-choice-green,
  "orange": tokens.$color-choice-orange,
);

@mixin m-button-colors($colors) {
  @each $color in $colors {
    &--variant-#{$color} {
      $fill-color: map-get($button-variants, $color);

      @include utils.m-hoverable(
        $fill-color: $fill-color,
        $hover-color: color.f-get-hover-color($fill-color)
      );
    }
  }
}

@mixin m-button-style($height, $padding) {
  height: $height;
  padding: 0 $padding;
}

.gnt-button {
  @include m-button-colors("primary" "secondary" "red" "green" "orange");
  @include reset.m-reset-boxsizing();
  @include utils.m-container-center;
  border-color: transparent;
  border-radius: tokens.$size-choice-border-radius-4;
  border-style: solid;
  border-width: tokens.$size-choice-border-width-2;

  &:focus {
    outline: none;
  }

  &--is-disabled {
    @include utils.m-unhoverable;
    background-color: transparent;
    border-color: tokens.$color-choice-white-25;
    pointer-events: none;
  }

  &--size-small {
    @include m-button-style(tokens.$size-choice-height-24, tokens.$size-choice-padding-24);
  }

  &--size-medium {
    @include m-button-style(tokens.$size-choice-height-32, tokens.$size-choice-padding-32);
  }

  &--size-large {
    @include m-button-style(tokens.$size-choice-height-40, tokens.$size-choice-padding-40);
  }

  &--size-xlarge {
    @include m-button-style(tokens.$size-choice-height-48, tokens.$size-choice-padding-48);
  }
}
