@use "../style/color";

@mixin m-set-iconcolor($color: $color-choice-white-100) {
  --icon-color: #{$color};
}

@mixin m-palette-iconcolors($colors) {
  @each $palettecolor in color.$palette-colors {
    @if (index($colors, $palettecolor)) {
      &--color-#{$palettecolor} {
        @include m-set-iconcolor($color: map-get(color.$palette-tokens, $palettecolor));
      }
    }
  }
}
