@use "../style/tokens";

$base: ".gnt-grid";

#{$base} {
  --columns: 3;
  --cellgap: 8;
  // stylelint-disable-next-line order/order
  align-items: stretch;
  display: grid;
  grid-gap: calc(var(--cellgap) * 1px);
  grid-template-columns: repeat(var(--columns), 1fr);
  justify-content: center;

  &--boxpack {
    grid-auto-flow: row dense;
  }
}

#{$base}__cell {
  --colspan: 1;
  --colspanadjusted: min(var(--colspan), var(--columns));
  --rowspan: 1;
  display: grid;
  grid-column-end: span var(--colspanadjusted);
  grid-row-end: span var(--rowspan);
  grid-template-areas: "main";

  > * {
    grid-area: main;
  }

  &::before {
    $ratio: calc(var(--rowspan) / var(--colspanadjusted));
    $extrawidth: calc((#{$ratio} - 1) * ((var(--cellgap) * 1px)));
    content: "";
    display: block;
    grid-area: main;
    padding-bottom: calc((#{$ratio} * 100%) + #{$extrawidth});
  }
}
