@use "../style/tokens";
@use "../style/color";

$base: ".gnt-select";

#{$base} {
  cursor: default;
  display: grid;
  // fix for Safari, should be covered by autoprefixer
  -webkit-user-select: none; // stylelint-disable-line property-no-vendor-prefix
  user-select: none;

  > div[role="listbox"]:focus {
    outline: none;
  }
}

#{$base}__toggle {
  align-items: center;
  background-color: tokens.$color-choice-grayscale-40;
  border: 0;
  border-radius: tokens.$size-choice-border-radius-4;
  display: grid;
  grid-template-areas: "main chevron";
  grid-template-columns: 1fr auto;
  padding: 0;
  text-align: initial;

  &--variant-small {
    height: tokens.$size-choice-height-24;
    padding-left: tokens.$size-choice-padding-8;
  }

  &--variant-large {
    height: tokens.$size-choice-height-32;
    padding-left: tokens.$size-choice-padding-12;
  }

  &:focus {
    outline: none;
  }
}

#{$base}:hover #{$base}__toggle {
  box-shadow: color.f-get-boxshadow("outline", "white25");
}

#{$base}:focus-within {
  #{$base}__toggle {
    box-shadow: color.f-get-boxshadow("outline", "blue");
  }

  #{$base}__label {
    color: tokens.$color-choice-white-100;
  }

  #{$base}__chevron {
    --icon-color: #{tokens.$color-choice-white-100};
  }
}

#{$base}__chevron {
  grid-area: chevron;
}

#{$base}__menu {
  background-color: tokens.$color-choice-grayscale-70;
  border-radius: tokens.$size-choice-border-radius-4;
  box-shadow: color.f-get-boxshadow("default");
  margin: 0;
  max-height: 180px;
  overflow: auto;
  padding: tokens.$size-choice-padding-8 0;
}

#{$base}__item {
  list-style: none;
  text-indent: 0;
}
