@use "../style/tokens";
@use "../style/color" as colors;
@use "sass:color";

$base: ".gnt-banner-card";

#{$base} {
  height: 400px;
}

#{$base}__content {
  max-width: min(560px, 50%);
  min-width: min(480px, 75%);
}

#{$base}__body {
  display: grid;
  grid-gap: tokens.$size-choice-padding-8;
}

#{$base}__actions {
  display: flex;
  grid-gap: tokens.$size-choice-padding-24;
  margin: tokens.$size-choice-padding-16 0 0 0;
  place-items: center left;
}
