@use "../style/tokens";

$base: ".gnt-segcontrol";

#{$base} {
  display: inline-grid;
  font-size: 0;
  grid-auto-columns: auto;
  grid-auto-flow: column;

  &--is-disabled {
    pointer-events: none;
  }
}

#{$base}__option {
  $rad: tokens.$size-choice-border-radius-4;
  background-color: tokens.$color-choice-grayscale-70;

  &:first-child {
    border-radius: $rad 0 0 $rad;
  }

  &:last-child {
    border-radius: 0 $rad $rad 0;
  }

  &--is-active {
    background-color: tokens.$color-choice-grayscale-40;
  }

  &--is-disabled {
    background-color: tokens.$color-choice-grayscale-70;
  }

  &:focus {
    outline: none;
  }
}

#{$base}__iconwrap {
  align-items: center;
  display: grid;
  height: tokens.$size-choice-height-32;
  justify-content: center;
  width: tokens.$size-choice-height-32;

  &:focus {
    outline: none;
  }
}
