// TODO: ensure stylelint and autoformatter agree on multi-line functions like conic-gradient() below

@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$base: ".gnt-brand-logo";

@mixin m-logo-hoverable($basecolor: tokens.$color-choice-white-100) {
  --bgcolor: #{$basecolor};
  --hovercolor: #{color.f-get-hover-color($basecolor)};
}

#{$base} {
  --bgcolor: transparent;
  --symbolcolor: #{tokens.$color-choice-white-100};
  background-color: var(--bgcolor);
  display: block;
  height: calc(var(--size) * 1px);
  width: calc(var(--size) * 1px);

  &--quixel {
    --hovercolor: transparent;
  }

  &--megascans {
    @include m-logo-hoverable(tokens.$color-choice-green);
  }

  &--bridge {
    @include m-logo-hoverable(tokens.$color-choice-blue);
  }

  &--mixer {
    @include m-logo-hoverable(tokens.$color-choice-red);
  }

  &--tracker {
    @include m-logo-hoverable(tokens.$color-choice-orange);
  }

  &--palette {
    --bgcolor: transparent;
    background-image: conic-gradient(
      from -90deg at 50% 50%,
      tokens.$color-choice-orange -44.92deg,
      tokens.$color-choice-green 44.85deg,
      tokens.$color-choice-blue 135.08deg,
      tokens.$color-choice-red 225.15deg,
      tokens.$color-choice-orange 315.08deg,
      tokens.$color-choice-green 404.85deg
    );
  }
}

#{$base}--is-hoverable {
  @include utils.m-transition(background-color background-image, out);

  &:hover {
    @include utils.m-transition(background-color background-image, in);
    background-color: var(--hovercolor);
  }

  &:hover#{$base}--quixel {
    --symbolcolor: #{color.f-get-hover-color(tokens.$color-choice-white-100)};
  }

  &:hover#{$base}--palette {
    --hovercolor: transparent;
    background-image: conic-gradient(
      from -90deg at 50% 50%,
      color.f-get-hover-color(tokens.$color-choice-orange) -44.92deg,
      color.f-get-hover-color(tokens.$color-choice-green) 44.85deg,
      color.f-get-hover-color(tokens.$color-choice-blue) 135.08deg,
      color.f-get-hover-color(tokens.$color-choice-red) 225.15deg,
      color.f-get-hover-color(tokens.$color-choice-orange) 315.08deg,
      color.f-get-hover-color(tokens.$color-choice-green) 404.85deg
    );
  }
}

#{$base}__symbol {
  @include utils.m-transition(fill, out);
  fill: var(--symbolcolor);

  #{$base}--is-hoverable:hover & {
    @include utils.m-transition(fill, in);
  }
}
