@use "../style/reset";
@use "../style/tokens";
@use "../style/color" as colors;
@use "../style/utils";
@use "sass:color";

$base: ".gnt-card";

#{$base} {
  background-color: tokens.$color-choice-grayscale-40;
  border-radius: tokens.$size-choice-border-radius-4 * 2;
  display: grid;
  grid-template-areas: "main";
  overflow: hidden;

  &:focus {
    outline: none;
  }

  &--is-hovered {
    box-shadow: colors.f-get-boxshadow("default"), colors.f-get-boxshadow("outline" "white25");
  }
  
  &--is-selected {
    box-shadow: colors.f-get-boxshadow("default"), colors.f-get-boxshadow("outline" "blue");
  }
}

#{$base}__image {
  --aspect-ratio: 1;
  align-items: stretch;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  border: initial;
  display: grid;
  grid-area: main;
  grid-template-areas: "main";
  outline: none;
  padding: 0;

  &--variant-contain {
    background-size: contain;
    margin: 10%;
  }

  &--variant-cover {
    background-size: cover;
  }

  &--variant-contain#{$base}__image--ar-horizontal {
    margin: 5%;
  }
}

#{$base}__image--has-gradient {
  $fill: rgba(tokens.$color-choice-pureblack, 0.6);
  $height: tokens.$size-choice-height-40 + tokens.$size-choice-padding-16;
  $gradient: colors.f-gradient-smooth($fill, color.adjust($fill, $alpha: -1));

  #{$base}:hover &:before,
  #{$base}--is-hovered &:before {
    background-image: linear-gradient(180deg, $gradient);
    content: "";
    grid-area: main;
    height: $height;
  }

  #{$base}:hover &:after,
  #{$base}--is-hovered &:after {
    align-self: end;
    background-image: linear-gradient(0deg, $gradient);
    content: "";
    grid-area: main;
    height: $height;
  }
}

#{$base}__regions {
  display: grid;
  grid-area: main;
  grid-template-areas:
    "ltop top rtop"
    "l mid r"
    "lbot bot rbot";
  grid-template-columns: auto 1fr auto;
  grid-template-rows: tokens.$size-choice-height-40 1fr tokens.$size-choice-height-40;
  pointer-events: none;
}

#{$base}__region {
  align-items: center;
  display: grid;
  grid-auto-flow: column;
  padding: tokens.$size-choice-padding-8;
  visibility: hidden;

  &--is-interactive > * {
    pointer-events: initial;
  }

  &--is-persistent,
  #{$base}--is-hovered &--is-overlay,
  #{$base}:hover &--is-overlay {
    visibility: initial;
  }

  &--position-top-left {
    grid-area: ltop;
    justify-self: flex-start;
  }

  &--position-top-right {
    grid-area: rtop;
    justify-self: flex-end;
  }

  &--position-bottom-left {
    grid-area: lbot;
    justify-self: flex-start;
  }

  &--position-bottom-right {
    grid-area: rbot;
    justify-self: flex-end;
  }
}

#{$base}__labelwrapper {
  padding: 0 tokens.$size-choice-padding-4;
}
