@use "../style/tokens";
@use "../style/color";
@use "../style/typography";

$base: ".gnt-combo-box";

#{$base} {
  align-items: center;
  background-color: tokens.$color-choice-grayscale-40;
  border-radius: tokens.$size-choice-border-radius-4;

  &--variant-small {
    height: tokens.$size-choice-height-24;
    padding-left: tokens.$size-choice-padding-8;
  }

  &--variant-large {
    height: tokens.$size-choice-height-32;
    padding-left: tokens.$size-choice-padding-12;
    padding-right: tokens.$size-choice-padding-4;
  }
}

#{$base}__combo {
  display: grid;
  grid-template-areas: "main toggle";
  grid-template-columns: minmax(0, 1fr) auto;
}

#{$base}:hover {
  box-shadow: color.f-get-boxshadow("outline", "white25");
}

#{$base}:focus-within {
  box-shadow: color.f-get-boxshadow("outline", "blue");

  #{$base}__input {
    color: tokens.$color-choice-white-100;
  }

  #{$base}__chevron {
    --icon-color: #{tokens.$color-choice-white-100};
  }
}

#{$base}__input {
  background-color: transparent;
  border: 0;
  color: tokens.$color-choice-white-50;
  font-family: tokens.$font-choice-family-normal;
  grid-area: main;
  padding: 0;

  &--has-value {
    color: tokens.$color-choice-white-100;
  }

  &--variant-small {
    @include typography.tvariant-body-small;
    height: tokens.$size-choice-height-24;
  }

  &--variant-large {
    @include typography.tvariant-body-medium;
    height: tokens.$size-choice-height-32;
  }

  &:focus {
    outline: none;
  }
}

#{$base}__button {
  background: none;
  border-left: 2px solid tokens.$color-choice-grayscale-50;
  display: grid;
  grid-area: toggle;
  padding: 0;
  place-items: center;
}

#{$base}__menu {
  background-color: tokens.$color-choice-grayscale-70;
  border-radius: tokens.$size-choice-border-radius-4;
  box-shadow: color.f-get-boxshadow("default");
  margin: 0;
  max-height: 180px;
  overflow: auto;
  padding: tokens.$size-choice-padding-8 0;
}

#{$base}__menu-item {
  list-style: none;
}

#{$base}__not-found-msg {
  padding: tokens.$size-choice-padding-4 tokens.$size-choice-padding-8;
  text-align: center;
}
