@use "sass:list";
@use "../style/tokens";
@use "../style/color";

$icon-hover-fade-in: tokens.$time-choice-60ms;
$icon-hover-fade-out: tokens.$time-choice-120ms;

/// Centers all children on a row inside the element
/// @output 'align-items', 'display', 'flex-flow' and 'justify-content'
@mixin m-container-center {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;

  &.l-align-start {
    justify-content: flex-start;
  }
}

/// Helper function for generating transition parameter strings
/// @param {list} $props - list of CSS properties to include in the transition
/// @param {String} $time - 'in' or 'out' for default fadetimes, or a CSS duration
/// @example
/// f-transition-params(color opacity, in);
/// f-transition-params(background-color, 200ms);
@function f-transition-params($props, $time: in) {
  $transitions: ();
  $fade-time: if($time == in, $icon-hover-fade-in, if($time == out, $icon-hover-fade-out, $time));

  @each $prop in $props {
    $t: #{$prop} #{$fade-time};
    $transitions: list.append($transitions, $t, comma);
  }
  @return $transitions;
}

/// Sets the default fadetimes as transitions on the specified props
///
/// @param {String} $props - list of CSS properties to include in the transition
/// @param {String} $time - 'in' or 'out' for default fadetimes, or a CSS duration
/// @output 'transition'
@mixin m-transition($props, $time) {
  transition: f-transition-params($props, $time);

  // Avoid Safari rendering glitches
  // https://stackoverflow.com/questions/54809093/simple-css-animation-not-smooth-in-safari
  will-change: f-transition-params($props, "");
}

@mixin m-hoverable-onhover() {
  &:hover:not(:active) {
    @content;
  }
}

/// Adds a background color for normal and hover states and box shadow for hover states
/// @param {Color} $fill-color - Base background color
/// @param {Color} $hover-color - Background color upon hover
/// @param {any} $prop - The property to be set, default is 'background-color'
/// @output 'background-color' (default), 'box-shadow' and 'transition'
@mixin m-hoverable($fill-color, $hover-color, $prop: background-color) {
  @include m-transition($prop, out);

  #{$prop}: $fill-color;
  @include m-hoverable-onhover() {
    #{$prop}: $hover-color;
    @include m-transition($prop, in);
  }

  &:hover {
    box-shadow: color.f-get-boxshadow("default");
  }
}

/// Prevents a box shadow and an overlay from appearing on hover
/// @output 'box-shadow' and 'background'
@mixin m-unhoverable() {
  &:hover {
    background: none;
    box-shadow: initial;
  }
}

/// Resets button styling
@mixin m-btn-reset() {
  background: none;
  border: 0;
  display: block;
  font: inherit;
  padding: 0;
  text-align: initial;

  // button is a replaced element which means that
  // it has intrinsinc dimensions dictated by a
  // context outside of CSS which we need to
  // explicitly override using width: 100%;
  // despite the fact that we already have
  // display: block;
  //
  // See: https://stackoverflow.com/a/27605483
  width: 100%;
}
