@use "../style/tokens";
@use "../style/utils";

$base: ".gnt-tabcontrol";

#{$base}__list {
  align-items: stretch;
  column-gap: tokens.$size-choice-padding-24;
  display: inline-grid;
  font-size: 0;
  grid-auto-flow: column;
  justify-content: center;
  padding: 0 tokens.$size-choice-padding-24;

  &--size-large {
    height: tokens.$size-choice-height-48;
  }

  &--size-medium {
    height: tokens.$size-choice-height-40;
  }

  &--size-small {
    height: tokens.$size-choice-height-32;
  }
}

#{$base}__tab {
  cursor: default;
  display: grid;
  place-items: center;

  &:focus {
    outline: none;
  }
}

#{$base}__label {
  @include utils.m-transition(color, out);

  #{$base}__tab:focus &,
  #{$base}__tab:hover & {
    @include utils.m-transition(color, in);
    color: tokens.$color-choice-white-100;
  }
}

#{$base}__cursor {
  @include utils.m-transition(left width, out);
  background-color: tokens.$color-choice-blue;
  height: tokens.$size-choice-border-width-2;
  position: absolute;

  &--size-large {
    transform: translate(0, tokens.$size-choice-height-48 - 2px);
  }

  &--size-medium {
    transform: translate(0, tokens.$size-choice-height-40 - 2px);
  }

  &--size-small {
    transform: translate(0, tokens.$size-choice-height-32 - 2px);
  }
}
