@use "sass:list";
@use "sass:map";
@use "sass:color";
@use "../style/tokens";

$shadow-presets: (
  "default": (
    "offset-x": tokens.$size-choice-box-shadow-default-offset-x,
    "offset-y": tokens.$size-choice-box-shadow-default-offset-y,
    "blur-radius": tokens.$size-choice-box-shadow-default-blur-radius,
    "spread-radius": 0,
    "color": rgba(tokens.$color-choice-pureblack, 0.24),
  ),
  "outline": (
    "offset-x": 0,
    "offset-y": 0,
    "blur-radius": 0,
    "spread-radius": "2px",
    "color": tokens.$color-choice-grayscale-60,
    "variants": (
      "gray60": (
        "color": tokens.$color-choice-grayscale-60,
      ),
      "gray70": (
        "color": tokens.$color-choice-grayscale-70,
      ),
      "blue": (
        "color": tokens.$color-choice-blue,
      ),
      "red": (
        "color": tokens.$color-choice-red,
      ),
      "orange": (
        "color": tokens.$color-choice-orange,
      ),
      "white25": (
        "color": tokens.$color-choice-white-25,
      )
    ),
  ),
);

@function f-make-boxshadow($style) {
  @if type-of($style) != "null" {
    $ofx: map-get($style, "offset-x");
    $ofy: map-get($style, "offset-y");
    $brad: map-get($style, "blur-radius");
    $spread: map-get($style, "spread-radius");
    $color: map-get($style, "color");
  
    @return #{$ofx} #{$ofy} #{$brad} #{$spread} #{$color};
  }
  @return "";
}

/// Constructs box-shadow parameters using a named preset.
///
/// Optionally, a preset variant can also be specified (e.g. a particular color or direction)
///
/// @param {list} $preset-name -- name of preset
/// @param {list} $variant-name -- name of variant (optional)
/// @return {String} box-shadow parameter string
///
/// @example scss - Variant
///   f-get-boxshadow("outline" "blue");
///   // 0 0 0 2px #26bbff
///

@function f-get-boxshadow($preset-name, $variant-name: default) {
  @if type-of($preset-name) == "list" {
    $variant-name: list.nth($preset-name, 2);
    $preset-name: list.nth($preset-name, 1);
  }
  $preset: map-get($shadow-presets, $preset-name);
  
  @if ($variant-name != "default") {
    $variants: map-get($preset, "variants");
    $current-variant: map-get($variants, $variant-name);
    @if $current-variant {
      $preset: map.merge($preset, $current-variant);
    }
  }
  @return f-make-boxshadow($preset);
}

$palette-colors: "blue" "red" "green" "orange" "black" "white" "white50" "white25" "white10";
$palette-tokens: (
  "blue": #{tokens.$color-choice-blue},
  "red": #{tokens.$color-choice-red},
  "green": #{tokens.$color-choice-green},
  "orange": #{tokens.$color-choice-orange},
  "black": #{tokens.$color-choice-black},
  "white": #{tokens.$color-choice-white-100},
  "white70": #{tokens.$color-choice-white-70},
  "white50": #{tokens.$color-choice-white-50},
  "white25": #{tokens.$color-choice-white-25},
  "white10": #{tokens.$color-choice-white-10},
);

@mixin m-palette-colors($colors, $attr: color) {
  @each $palettecolor in $palette-colors {
    @if (index($colors, $palettecolor)) {
      &--color-#{$palettecolor} {
        #{$attr}: map-get($palette-tokens, $palettecolor);
      }
    }
  }
}

$hover-colors: (
  tokens.$color-choice-white-10: tokens.$color-choice-white-15,
  tokens.$color-choice-white-100: #{rgba(tokens.$color-choice-white-100, 0.85)},
  tokens.$color-choice-blue: tokens.$color-choice-blue-hover,
  tokens.$color-choice-green: tokens.$color-choice-green-hover,
  tokens.$color-choice-red: tokens.$color-choice-red-hover,
  tokens.$color-choice-orange: tokens.$color-choice-orange-hover,
);

@function f-get-hover-color($base-color) {
  @return map-get($hover-colors, $base-color) or scale-color($base-color, $lightness: 25%);
}

@function f-gradient-smooth($start, $end) {
  $nodes: (
    (1, 0),
    (0.738, 19),
    (0.541, 34),
    (0.382, 47),
    (0.278, 56.5),
    (0.194, 65),
    (0.126, 73),
    (0.075, 80.2),
    (0.042, 86.1),
    (0.021, 91),
    (0.008, 95.2),
    (0.002, 98.2),
    (0, 100)
  );

  $steps: ();
  @each $node in $nodes {
    $blend: nth($node, 1);
    $offset: nth($node, 2);
    $step: #{mix($start, $end, $blend * 100)} #{$offset}#{"%"};
    $steps: append($steps, $step, comma);
  }
  @return $steps;
}
