@use "../style/tokens";
@use "sass:map";

$colors: (
  "blue": tokens.$color-choice-blue,
  "orange": tokens.$color-choice-orange,
  "green": tokens.$color-choice-green,
  "red": tokens.$color-choice-red,
  "yellow": tokens.$color-choice-yellow,
  "brown": tokens.$color-choice-brown,
  "pink": tokens.$color-choice-pink,
  "purple": tokens.$color-choice-purple,
  "gray": tokens.$color-choice-grayscale-90,
  "black": tokens.$color-choice-grayscale-30,
  "white": tokens.$color-choice-white-100,
);

$base: ".gnt-color-tag";

#{$base} {
  height: tokens.$size-choice-height-24;
  width: tokens.$size-choice-height-24;

  @each $color, $val in $colors {
    &--color-#{$color} {
      --bgcolor: #{$val};
    }
  }

  &--has-emphasis circle {
    paint-order: stroke;
    stroke: tokens.$color-choice-white-25;
    stroke-width: tokens.$size-choice-border-width-2; // result is 1px since fill covers half of it
  }
}

#{$base}__path {
  #{$base}--variant-small & {
    fill: var(--bgcolor);
  }
  
  #{$base}--variant-large & {
    fill: var(--bgcolor);
  }
}
