@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$base: ".gnt-icon-button";

$variants: (
  "primary": tokens.$color-choice-blue,
  "secondary": tokens.$color-choice-grayscale-70,
  "green": tokens.$color-choice-green,
  "orange": tokens.$color-choice-orange,
  "red": tokens.$color-choice-red,
);


#{$base} {
  --fillcolor: transparent;
  --bordercolor: transparent;
  --hovercolor: transparent;
  $block: &;
  background-color: transparent;
  border-radius: 50%;
  border-style: none;
  display: grid;
  grid-template-areas: "main";
  height: var(--size);
  padding: 0;
  place-items: center;
  stroke-width: 1.5;
  width: var(--size);

  @include utils.m-hoverable-onhover() {
    --fillcolor: var(--hovercolor);
    --bordercolor: var(--hovercolor);
    #{$base}__circle {
      @include utils.m-transition(fill stroke, in);
    }
  }

  &:focus {
    outline: none;
  }

  @each $variant, $color in $variants {
    &--variant-#{$variant} {
      --fillcolor: #{$color};
      --bordercolor: #{$color};
      --hovercolor: #{color.f-get-hover-color($color)};
    }
  }

  &--size-small {
    --size: #{tokens.$size-choice-height-24};
  }

  &--size-large {
    --size: #{tokens.$size-choice-height-32};
  }

  &--is-disabled {
    --fillcolor: transparent;
    --bordercolor: #{tokens.$color-choice-white-25};
    @include utils.m-unhoverable;
    box-shadow: none;
    pointer-events: none;
  }

  &--has-shadow {
    box-shadow: color.f-get-boxshadow("default");
  }
}

#{$base}__plate {
  grid-area: main;
}

#{$base}__circle {
  @include utils.m-transition(fill stroke, out);
  fill: var(--fillcolor);
  height: var(--size);
  stroke: var(--bordercolor);
  stroke-linecap: round;
  stroke-linejoin: round;
  width: var(--size);
}

#{$base}__icon {
  grid-area: main;
}
