@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$base: ".gnt-icon";

#{$base} {
  --icon-color: none;
  --icon-color-secondary: #{tokens.$color-choice-black};
  height: tokens.$size-choice-height-24;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 1.5px;
  width: tokens.$size-choice-height-24;

  @each $color-name in color.$palette-colors {
    &--color-#{$color-name} {
      --icon-color: #{map-get(color.$palette-tokens, $color-name)};
      &#{$base}--is-clickable:hover {
        --icon-color: #{tokens.$color-choice-white-100};
      }
    }
  }
}

#{$base}__path {
  #{$base}--is-clickable & {
    @include utils.m-transition(fill stroke, out);
  }

  #{$base}--is-clickable:hover & {
    @include utils.m-transition(fill stroke, in);
  }

  &--fill {
    fill: var(--icon-color);
  }

  &--stroke {
    stroke: var(--icon-color);
  }

  &--stroke-secondary {
    stroke: var(--icon-color-secondary);
  }

  &--fill-secondary {
    fill: var(--icon-color-secondary);
  }
}
