@use "../style/tokens";

$base: ".gnt-stack";

#{$base} {
  align-items: center;
  display: grid;
  grid-gap: tokens.$size-choice-padding-16;
  justify-content: start;
}

#{$base}--h {
  grid-auto-flow: column;
}

#{$base}--v {
  grid-auto-flow: row;
}

#{$base}--separators {
  grid-gap: tokens.$size-choice-padding-12;
}

#{$base}--space-between {
  justify-content: space-between;
}

#{$base}__separator {
  background: tokens.$color-choice-white-50;
  height: tokens.$size-choice-line-10;
  width: 1px;
}
