@use "../style/tokens";
@use "../style/utils";

$base: ".gnt-radio-button";

#{$base} {
  display: inline-block;
  font-size: 0;

  &[aria-disabled="true"] {
    pointer-events: none;
  }

  &:focus {
    outline: none;
  }
}

#{$base}__inner {
  align-items: flex-start;
  column-gap: tokens.$size-choice-padding-8;
  display: inline-grid;
  grid-auto-flow: column;

  &:focus {
    outline: none;
  }
}

#{$base}__input {
  display: none;
}

#{$base}__icon {
  @include utils.m-transition(opacity, out);

  &--is-disabled {
    opacity: 0.25;
  }

  #{$base}[aria-checked="false"] &--is-enabled:hover {
    --icon-color: #{tokens.$color-choice-white-100};
    @include utils.m-transition(opacity, in);
    opacity: 1;
  }
}

#{$base}__icon path {
  @include utils.m-transition(fill stroke, out);
  #{$base}:hover & {
    @include utils.m-transition(fill stroke, in);
  }
}

#{$base}__label {
  color: tokens.$color-choice-white-100;
  user-select: none;

  &--medium {
    margin-top: (tokens.$size-choice-height-24 - tokens.$size-choice-line-20) / 2;
  }

  &--small {
    margin-top: (tokens.$size-choice-height-24 - tokens.$size-choice-line-18) / 2;
  }

  #{$base}[aria-disabled="true"] & {
    color: tokens.$color-choice-white-25;
  }
}
