@use "../style/tokens";
@use "../style/color";

$base: ".gnt-progress";

#{$base} {
  --value: 0;
  --meter-color: #{tokens.$color-choice-blue};

  display: grid;
  grid-template-areas: "main";

  &--variant-linear {
    border-radius: tokens.$size-choice-border-radius-4;
    height: tokens.$size-choice-height-24 / 6;
    min-width: tokens.$size-choice-height-48 * 2;
    overflow: hidden;
    will-change: transform; // Force Safari to clip with border-radius
  }

  &--variant-radial {
    height: var(--size);
    stroke-width: 1.5;
    width: var(--size);
  }

  &--size-small {
    --size: #{tokens.$size-choice-height-24};
  }

  &--size-large {
    --size: #{tokens.$size-choice-height-32};
  }
}
#{$base}__glyph {
  grid-area: main;
}

#{$base}__track {
  &--variant-linear {
    background-color: tokens.$color-choice-white-10;
    border-radius: tokens.$size-choice-border-radius-4;
    grid-area: main;
    height: tokens.$size-choice-height-24 / 6;
  }

  &--variant-radial {
    fill: transparent;
    stroke: tokens.$color-choice-white-10;
    stroke-linecap: round;
  }
}
#{$base}__meter {
  $block: &;
  @each $c in color.$palette-colors {
    &--color-#{$c} {
      --meter-color: #{map-get(color.$palette-tokens, $c)};
      stroke: url("#gnt-progress__gradient--color-#{$c}");
    }
  }

  &#{$block}--variant-linear {
    border-radius: tokens.$size-choice-border-radius-4;
    grid-area: main;

    &#{$block}--has-value {
      background-color: var(--meter-color);
      transition: width 240ms;
      width: calc(var(--value) * 100%);
      will-change: width;
    }

    &#{$block}--no-value {
      animation: gnt-progress__anim-runner 1.8s linear infinite;
      background-image: linear-gradient(90deg, transparent, var(--meter-color));
      background-position: right;
      background-repeat: repeat-y;
      background-size: 100% 100%;
      will-change: transform;
    }
  }

  &#{$block}--variant-radial {
    --origin: calc(var(--size) / 2);
    fill: transparent;
    transform-origin: var(--origin) var(--origin);
    will-change: transform;

    &#{$block}--has-value {
      stroke: var(--meter-color);
      transform: rotate(-90deg);
    }

    &#{$block}--no-value {
      animation: gnt-progress__anim-rot 1.2s linear infinite;
    }

    &#{$block}--is-running {
      stroke-linecap: round;
    }
  }
}

@keyframes gnt-progress__anim-rot {
  to {
    transform: rotate(360deg);
  }
}

@keyframes gnt-progress__anim-runner {
  from {
    transform: translate(-100%, 0);
  }

  to {
    transform: translate(100%, 0);
  }
}

@each $c in color.$palette-colors {
  #gnt-progress__gradient--color-#{$c} {
    --meter-color: #{map-get(color.$palette-tokens, $c)};
  }
}

#{$base}__gradient-stop {
  stop-color: var(--meter-color);

  &__start {
    stop-opacity: 0;
  }
}
