  font-weight: tokens.$font-choice-weight-700;
  letter-spacing: tokens.$size-choice-tracking-0;
  line-height: tokens.$size-choice-line-26;
}
@mixin tvariant-heading-medium {
  font-size: tokens.$size-choice-text-36;
  font-weight: tokens.$font-choice-weight-700;
  letter-spacing: tokens.$size-choice-tracking-minus1;
  line-height: tokens.$size-choice-line-36;
}
@mixin tvariant-heading-large {
  font-size: tokens.$size-choice-text-48;
  font-weight: tokens.$font-choice-weight-700;
  letter-spacing: tokens.$size-choice-tracking-minus2;
  line-height: tokens.$size-choice-line-48;
}
