@use "../style/tokens";
@use "../style/utils";
@use "../style/color";

$base: ".gnt-pill";

#{$base} {
  @include utils.m-hoverable(
    $fill-color: tokens.$color-choice-white-10,
    $hover-color: color.f-get-hover-color(tokens.$color-choice-white-10)
  );
  align-items: center;
  border-radius: calc(var(--size) / 2);
  display: inline-grid;
  grid-template-columns: 1fr auto;
  height: var(--size);
  justify-content: space-between;
  user-select: none;

  &--variant-large {
    --size: #{tokens.$size-choice-height-24};
    padding: 0 tokens.$size-choice-padding-12;

    &#{$base}--has-icon {
      gap: #{tokens.$size-choice-padding-4 / 2};
      padding-right: tokens.$size-choice-padding-4;
    }
  }

  &--variant-medium {
    --size: #{tokens.$size-choice-height-40 / 2};
    padding: 0 #{tokens.$size-choice-padding-40 / 4};

    &#{$base}--has-icon {
      padding-right: #{tokens.$size-choice-padding-4 / 2};
    }
  }

  &--variant-small {
    --size: #{tokens.$size-choice-height-32 / 2};
    padding: 0 tokens.$size-choice-padding-8;

    &#{$base}--has-icon {
      padding-right: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

#{$base}__icon {
  #{$base}--variant-small & {
    margin: -4px 0;
  }

  #{$base}--variant-medium & {
    margin: -2px 0;
  }
}
