@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$base: ".gnt-progressbutton";

#{$base} {
  align-items: center;
  column-gap: 4px;
  display: grid;
  grid-template-areas: "main" "caption";
  justify-content: center;
  overflow: hidden;
  row-gap: tokens.$size-choice-padding-12 / 2;
  text-overflow: ellipsis;
  white-space: nowrap;

  &--size-large {
    padding: tokens.$size-choice-padding-4 0 0;
    width: tokens.$size-choice-padding-24 * 3;
  }
  
  &--size-large > .gnt-textlabel {
    text-align: center;
  }

  &--size-small {
    grid-template-areas: "caption main";
    grid-template-columns: 1fr auto;
  }

  &--size-small > .gnt-textlabel {
    text-align: right;
  }
}

#{$base}__button {
  display: grid;
  grid-area: main;
  grid-template-areas: "main";
  place-items: center;
}

#{$base}__start-button {
  @include utils.m-transition(opacity, out);
  grid-area: main;
  opacity: 0;

  &--state-initial {
    opacity: 1;
  }

  #{$base} &--state-completed:hover,
  #{$base} &--state-failed:hover {
    @include utils.m-transition(opacity, in);
    opacity: 1;
  }
}

#{$base}__stop-button {
  @include utils.m-transition(opacity, out);
  grid-area: main;
  opacity: 0;
  pointer-events: none;

  #{$base}--size-small &--state-running {
    pointer-events: initial;
  }

  #{$base}--size-small &--state-running:hover,
  #{$base}--size-large &--state-running {
    @include utils.m-transition(opacity, in);
    opacity: 1;
    pointer-events: initial;
  }
}

#{$base}__progress {
  @include utils.m-transition(opacity, out);
  grid-area: main;

  &--state-initial {
    display: none;
  }
  &--state-initial,
  #{$base}__button:hover &--state-completed,
  #{$base}__button:hover &--state-failed {
    @include utils.m-transition(opacity, in);
    opacity: 0;
  }
}
#{$base}__icon {
  grid-area: main;
  opacity: 0;

  #{$base} &--state-completed:not(:hover),
  #{$base} &--state-failed:not(:hover) {
    @include utils.m-transition(opacity, in);
    opacity: 1;
  }
}

#{$base}__caption {
  grid-area: caption;
}
