@use "../style/tokens";
@use "../style/color";

$base: ".gnt-asseticon";

#{$base} {
  display: grid;
  grid-template-areas: "icon" "label";
  height: tokens.$size-choice-height-48 + tokens.$size-choice-height-24;
  place-items: center;
  width: tokens.$size-choice-height-48 + tokens.$size-choice-height-24;
}

#{$base}__icon {
  grid-area: icon;
}

#{$base}__label {
  grid-area: label;
}

#{$base}__variations {
  grid-area: icon;
}

#{$base}asset__path {
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: tokens.$size-choice-border-width-2;

  &--fill {
    fill: tokens.$color-choice-white-50;
  }

  &--stroke {
    stroke: tokens.$color-choice-white-50;
  }
}
