@use "../style/tokens";
@use "../style/utils";

@mixin avatar-size($fontsize, $size) {
  font-size: $fontsize;
  height: $size;
  line-height: calc(#{$fontsize} - 0.1px);
  width: $size;
}

.gnt-avatar {
  background-color: tokens.$color-choice-grayscale-70;
  border-radius: 50%;
  overflow: hidden;

  &__initials {
    @include utils.m-container-center;
    border-radius: 50%;
    color: tokens.$color-choice-white-100;
    font-family: tokens.$font-choice-family-normal;
    height: 100%;
    width: 100%;
  }

  &__image {
    background-size: 100%;
    border-radius: 50%;
    display: block;
    height: 100%;
    width: 100%;
  }

  &__image--has-initials {
    transform: translate(0, -100%);
  }

  &--size-xlarge {
    @include avatar-size(56px, 128px);
  }

  &--size-large {
    @include avatar-size(24px, 64px);
  }

  &--size-medium {
    @include avatar-size(16px, 48px);
  }

  &--size-small {
    @include avatar-size(12px, 32px);
  }

  &--size-xsmall {
    @include avatar-size(8px, 20px);
  }
}
