@use "../style/reset";
@use "../style/tokens";
@use "../style/color";
@use "../style/utils";

$base: ".gnt-toast";

#{$base} {
  background-color: tokens.$color-choice-grayscale-70;
  border-radius: tokens.$size-choice-border-radius-4;
  box-shadow: color.f-get-boxshadow(("default"));
  padding: tokens.$size-choice-padding-16;
  padding-right: tokens.$size-choice-padding-24;
  position: relative;
  width: 320px;
}

#{$base}__close {
  position: absolute;
  right: tokens.$size-choice-padding-4;
  top: tokens.$size-choice-padding-4;
}
